"use client";

import Image from "next/image";
import { useRef } from "react";

type KnownForItem = {
  id: number;
  title: string;
  poster_path: string;
};

type Props = {
  items: KnownForItem[];
};

export default function CastKnownForCarousel({ items }: Props) {
  const sliderRef = useRef<HTMLDivElement>(null);

  const scroll = (offset: number) => {
    sliderRef.current?.scrollBy({
      left: offset,
      behavior: "smooth",
    });
  };

  return (
    <section className="relative w-full">
      <h2 className="text-xl font-bold mb-4">Known For</h2>

      {/* Left Arrow */}
      <button
        onClick={() => scroll(-320)}
        className="absolute left-0 top-1/2 -translate-y-1/2 z-10
                   bg-black/60 text-white w-9 h-9 rounded-full"
        aria-label="Scroll left"
      >
        ‹
      </button>

      {/* Right Arrow */}
      <button
        onClick={() => scroll(320)}
        className="absolute right-0 top-1/2 -translate-y-1/2 z-10
                   bg-black/60 text-white w-9 h-9 rounded-full"
        aria-label="Scroll right"
      >
        ›
      </button>

      {/* Scroll Container */}
      <div
        ref={sliderRef}
        className="
          flex gap-4 overflow-x-auto px-10 pb-4
          scroll-smooth
          scrollbar-thin
        "
      >
        {items.map((movie) => (
          <div key={movie.id} className="flex-shrink-0 w-[140px]">
            {/* Poster */}
            <div className="relative aspect-[2/3] rounded-xl overflow-hidden shadow-md">
              <Image
                src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`}
                alt={movie.title}
                fill
                sizes="140px"
                className="object-cover"
                priority={false}
              />
            </div>

            {/* Title */}
            <p className="mt-2 text-sm text-center leading-tight">
              {movie.title}
            </p>
          </div>
        ))}
      </div>
    </section>
  );
}
